
.tp .wz{
    font-size: 12px;
  position: absolute;
   bottom: 0; left: 35%;
}
.tp{
    overflow: hidden;
}
.tp div{
    border: 1px solid #000;
    float: left;
    width: 31%;
    height: 45px;
    margin-left:1%; 
    margin-right:.6%; 
    margin-top:1%; 
    position: relative;
}
.top{
    overflow: hidden;
}
.top div{
    
    text-align: center;
    border: 1px solid #cdcdcd;
}
.left{
    font-size: 20px;
    line-height: 40px;
    width: 48%;
    float: left;
    height: 40px;
}
.right{
    font-size: 20px;
     line-height: 40px;
    height: 40px;
}



