
.img{
  display:-webkit-box;
  display:-webkit-flex;
  display:flex;
  -webkit-box-align:center;
  -webkit-align-items:center;
          align-items:center; 
  -webkit-box-pack:center; 
  -webkit-justify-content:center; 
          justify-content:center;
}
.img img{
  width:100%;
  height:auto;
}
.a {
  font-size: 23px;
  color: red;
}
.b{
  font-size: 20px;
  color:black;
}
.head{
  width: 100%;
  margin-top:3px; 
  overflow:hidden
}
.kong{
  width:100%;
  height: 10px;
  background: rgba(153, 153, 153, 0.404)
}
.zhong {
  line-height: 50px;
  width: 100%;
  height: 50px;
}
.zhong a{
 color: rgba(74, 155, 155, 0.555);
 font-size: 20px
}
.low{
  background: blue;
  color:white;
  font-size: 23px;
  width: 100%;
  height: 70px;

  text-align: center;
  position: fixed;
  bottom:0;
}
.head-left{
  width: 50%;
  float: left;
}
.head-right{
  width: 50%;
  float: right;
  margin: 0 auto;
}
.head-right button{
  border-radius: 5px;
  margin-top:5px; 
  width: 125px;
  background: blue;
  color: white;
  line-height: 44px;
  font-size: 25px;
  float:right;
}




